@use "base/variables" as v;
@use "base/mixins" as m;

.mobile {
  display: block;
  @include m.desktop {
    display: none;
  }
  transition: all 0.25s ease-in;
  position: fixed;
  top: -8rem;
  background-color: v.$grisOscuro;
  padding: 3rem;
  width: 100%;
  &__grid {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
  }
  &__link {
    color: white;
    padding: 1rem;
    text-align: center;
    &:hover {
      background-color: rgb(119, 119, 119);
      width: 100%;
    }
  }
  &__separador {
    border: solid rgb(141, 141, 141) 1px;
    width: 100%;
  }
}
.active {
  z-index: 2;
  top: 23rem;
  @include m.tablet {
    top: 20rem;
  }
}
