@use "base/variables" as v;
@use "base/mixins" as m;

.thanks {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &__heading {
    span {
      font-weight: 300;
      display: block;
    }
  }
  &__info {
    text-align: center;
  }
  &__btn {
    background-color: v.$primario;
    color: white;
    text-align: center;
    padding: 1.2rem;
    width: 100%;
    border-radius: 0.2rem;
    font-size: 2rem;
  }
}
