@use "base/variables" as v;
@use "base/mixins" as m;

.header {
  z-index: 10;
  width: 100%;
  position: fixed;
  top: 0;
  background-color: v.$primario;
  padding-bottom: 0.3rem;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__login {
    display: flex;
    align-items: center;
  }
  &__icon {
    width: 2rem;
    height: 2rem;
    margin-right: 1rem;
  }
  &__text {
    color: white;
    span {
      font-weight: 200;
    }
  }
  &__space {
    border: solid 0.1px #3d3d3d;
    width: 100%;
  }
}

.navigation {
  margin-top: 2rem;
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  @include m.tablet {
    flex-direction: row;
  }
  &__top {
    display: flex;
    gap: 15rem;
  }
  &__link--activo {
    background-color: white;
    color: v.$primario;
    font-size: 2.5rem;
    padding: 0.5rem 1rem;
    &:hover {
      opacity: 60%;
    }
  }
  &__logo {
    width: 6rem;
  }
  &__links {
    display: none;
    @include m.desktop {
      display: flex;
    }
    flex-direction: column;
    gap: 2rem;
    align-items: center;
    @include m.tablet {
      flex-direction: row;
    }
    @include m.desktop {
    }
  }
  &__link {
    color: white;
    text-align: center;
    font-size: 2.5rem;
    position: relative;
    &:hover {
      color: rgb(211, 211, 211);
    }
  }
  &__icons {
    cursor: pointer;
    display: flex;
    gap: 2rem;
    align-items: center;
  }
  &__icon1 {
    cursor: pointer;
    width: 2rem;
    height: 2rem;
    margin-right: 0;
  }
  &__countProduct {
    margin-left: -1rem;
    padding: 0.4rem;
    color: v.$primario;
    background-color: white;
    text-align: center;
    border-radius: 0.3rem;
  }

  &__mobile {
    display: block;
    @include m.desktop {
      display: none;
    }
  }
  &__burger {
    width: 2rem;
    margin-left: 3.5rem;
  }
  &__mobile {
    :hover {
      opacity: 60%;
    }
  }
  &__containerCart {
    position: relative;
  }
  &__cardProducts {
    position: absolute;
    background-color: white;
    padding: 2rem;
    min-height: 60rem;
    min-width: 50rem;
    z-index: 1;
    right: 20rem;
    border-radius: 0.3rem;
    box-shadow: 2px 2px 10px 5px rgba(0, 0, 0, 0.068);
  }
  &__buyContainer {
    background-color: v.$primario;
    display: block;
    padding: 1rem;
    color: white;
    text-align: center;
    font-weight: 700;
    border-radius: 0.3rem;
    margin-bottom: 2rem;
    &:hover {
      opacity: 70%;
    }
  }
  &__clearContainer {
    border: solid 1px v.$primario;
    display: block;
    padding: 1rem;
    color: v.$primario;
    text-align: center;
    font-weight: 700;
    border-radius: 0.3rem;
    &:hover {
      opacity: 70%;
    }
  }
  &__arrow {
    transform: rotate(360deg);
    transition: all ease-in-out 0.4s;
  }
}

//cart
.submenu {
  position: relative;
  &__countProduct {
    padding: 0.4rem;
    color: v.$primario;
    background-color: white;
    text-align: center;
    border-radius: 0.3rem;
  }
  &__cartCount {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
}

.submenu #cart {
  display: none;
  cursor: pointer;
}
.submenu:hover #cart {
  display: block;
  position: absolute;
  right: -11.5rem;
  top: 100%;
  z-index: 1;
  background-color: v.$grisClaro;
  padding: 20px;
  min-height: 100%;
  min-width: 320px;
  border-radius: 0.3rem;
  box-shadow: 1px 2px 6px 2px rgba(0, 0, 0, 0.068);
  @include m.tablet {
    right: 0;
    min-width: 500px;
  }
  @include m.desktop {
    right: 0;
    min-height: 100%;
    min-width: 600px;
  }
}
th {
  text-transform: uppercase;
}
td {
  font-size: 2rem;
  text-transform: uppercase;
  border-bottom: 2px solid rgb(231, 231, 231);
  img {
    max-width: 100%;
    @include m.desktop {
      max-width: 50%;
    }
  }
}

.padre {
  text-align: end;
}
.enlace {
  display: inline-block;
  background-color: aqua;
  padding: 2rem;
}

.arrowActive {
  transform: rotate(180deg);
}
