@use "base/variables" as v;
@use "base/mixins" as m;

.newrelease {
  transition: all ease-in-out 0.6s;
  position: fixed;
  top: 43rem;
  right: 2%;
  z-index: 2;
  width: 95%;
  padding: 2rem 3rem;
  box-shadow: 2px 2px 7px 2px rgba(0, 0, 0, 0.075);
  border-radius: 0.3rem;
  background-color: v.$grisClaro;
  display: none;
  @include m.desktop {
    top: 21rem;
    display: block;
  }
  @include m.desktopXL {
    padding: 5rem 10rem;
  }

  &__flex {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
    justify-content: center;
    @include m.desktopSM {
      flex-direction: row;
      gap: 3rem;
    }
    @include m.desktop {
      flex-direction: row;
      gap: 5rem;
    }
    @include m.desktopXL {
      flex-direction: row;
      gap: 10rem;
    }
  }
  &__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    @include m.desktop {
      flex-direction: row;
    }
  }
  &__img {
    width: 30rem;
  }
  &__heading {
    font-size: 2rem;
    margin-bottom: 0;
  }
  &__separation {
    border-bottom: solid 1px rgb(211, 211, 211);
  }
  &__text {
    font-size: 1.5rem;
    font-weight: 300;
    text-align: justify;
  }
  &__coming {
    text-align: end;
    font-size: 1.8rem;
  }
}

.hideRelease {
  top: -60rem;
}
