@use "base/variables" as v;
@use "base/mixins" as m;

.galery {
  padding: 2rem 0;
  margin-top: 7rem;
  margin-bottom: 3rem;

  &__grid {
    position: relative;
    @include m.grid(1, 2rem);
    @include m.desktop {
      @include m.grid(3, 2rem);
    }
  }
  &__text {
    position: absolute;
    width: 30rem;
    height: 30rem;
    right: 1%;
    left: 1%;
    bottom: 0;
    color: white;
    z-index: 10;
    background-color: aqua;
  }
  &__link {
    overflow: hidden;
    &:nth-child(2) {
      display: none;
    }
    @include m.desktop {
      &:nth-child(2) {
        display: block;
      }
    }
    img {
      border-radius: 0.4rem;
      height: 30rem;
      width: 100%;
      object-fit: cover;

      @include m.desktop {
        transition-property: transform filter;
        transition-duration: 0.3s;
        transition-delay: 0.1s;
        height: 50rem;
        width: 50rem;
        &:hover {
          transform: scale(1.02);
          filter: brightness(50%);
        }
      }
    }
    @include m.desktop {
      &:nth-child(1) {
        grid-column: 1/3;
        img {
          width: 102rem;
        }
      }
    }
    @include m.desktop {
      &:nth-child(2) {
        grid-column: 3/4;
        grid-row: 1/3;
        img {
          height: 102rem;
        }
      }
    }
    @include m.desktop {
      &:nth-child(3) {
        grid-column: 1/2;
        grid-row: 1/2;
        img {
          width: 50rem;
        }
      }
    }
    @include m.desktop {
      &:nth-child(4) {
        grid-column: 2/3;
        grid-row: 1/2;
        img {
          width: 50rem;
        }
      }
    }
  }
}
