@use "base/variables" as v;
@use "base/mixins" as m;

.customer {
  background-color: v.$grisClaro;
  padding: 5rem 0;
  &__flex {
    overflow: hidden;
  }

  &__card {
    margin-top: 1rem;
    margin-bottom: 1rem;
    margin-right: 2rem;
    display: flex;
    background-color: white;
    gap: 2rem;
    border-radius: 0.3rem;
    padding: 2rem;
    align-items: center;
    box-shadow: 5px 3px 7px 2px rgba(54, 54, 54, 0.075);
    transition: all 0.3s ease-in-out;
    &:hover {
      box-shadow: 2px 0px 7px 2px rgba(54, 54, 54, 0.178);
    }
  }
  &__rev {
    display: flex;
    flex-direction: column;
  }
  &__avatar {
    width: 6rem;
    height: 6rem;
    border-radius: 0.3rem;
  }
  &__tittle {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__name {
    font-size: 1.8rem;
    margin-bottom: 0;
  }
  &__text {
    font-size: 1.4rem;
    font-weight: 300;
  }
  &__stars {
    width: 7rem;
    height: auto;
  }
  &__arrows {
    display: flex;
    gap: 3rem;
    margin-bottom: 1rem;
    justify-content: flex-end;
    margin-right: 2rem;
  }
  &__back,
  &__next {
    width: 3rem;
    height: 3rem;
    display: block;
    border: none;
    background: none;
    cursor: pointer;
    &:hover {
      opacity: 70%;
    }
  }
}
.customer__indicator .glider-dot {
  display: block;
  width: 3rem;
  height: 0.4rem;
  border-radius: 0.3rem;
}

.customer__indicator .glider-dot.active {
  background-color: v.$primario;
}
