@use "base/variables" as v;
@use "base/mixins" as m;

.btn__chat {
  position: fixed;
  bottom: 5rem;
  right: -14px;
  transition-property: transform;
  transition-duration: 0.3s;
  transition-delay: 0.1s;
  &:hover {
    transform: scale(1.05);
  }
  @include m.desktop {
    bottom: 1rem;
    right: 1rem;
  }
}
