.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #1d1d1b;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Oswald", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #242424;
  font-size: 2rem;
  text-transform: uppercase;
}

.container {
  width: 95%;
  max-width: 80%;
  margin: 0 auto;
}

a {
  text-decoration: none;
  text-transform: uppercase;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
  text-transform: uppercase;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

ul {
  list-style: none;
}

.header {
  z-index: 10;
  width: 100%;
  position: fixed;
  top: 0;
  background-color: #1d1d1b;
  padding-bottom: 0.3rem;
}
.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__login {
  display: flex;
  align-items: center;
}
.header__icon {
  width: 2rem;
  height: 2rem;
  margin-right: 1rem;
}
.header__text {
  color: white;
}
.header__text span {
  font-weight: 200;
}
.header__space {
  border: solid 0.1px #3d3d3d;
  width: 100%;
}

.navigation {
  margin-top: 2rem;
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .navigation {
    flex-direction: row;
  }
}
.navigation__top {
  display: flex;
  gap: 15rem;
}
.navigation__link--activo {
  background-color: white;
  color: #1d1d1b;
  font-size: 2.5rem;
  padding: 0.5rem 1rem;
}
.navigation__link--activo:hover {
  opacity: 60%;
}
.navigation__logo {
  width: 6rem;
}
.navigation__links {
  display: none;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
}
@media (min-width: 1024px) {
  .navigation__links {
    display: flex;
  }
}
@media (min-width: 768px) {
  .navigation__links {
    flex-direction: row;
  }
}
.navigation__link {
  color: white;
  text-align: center;
  font-size: 2.5rem;
  position: relative;
}
.navigation__link:hover {
  color: lightgray;
}
.navigation__icons {
  cursor: pointer;
  display: flex;
  gap: 2rem;
  align-items: center;
}
.navigation__icon1 {
  cursor: pointer;
  width: 2rem;
  height: 2rem;
  margin-right: 0;
}
.navigation__countProduct {
  margin-left: -1rem;
  padding: 0.4rem;
  color: #1d1d1b;
  background-color: white;
  text-align: center;
  border-radius: 0.3rem;
}
.navigation__mobile {
  display: block;
}
@media (min-width: 1024px) {
  .navigation__mobile {
    display: none;
  }
}
.navigation__burger {
  width: 2rem;
  margin-left: 3.5rem;
}
.navigation__mobile :hover {
  opacity: 60%;
}
.navigation__containerCart {
  position: relative;
}
.navigation__cardProducts {
  position: absolute;
  background-color: white;
  padding: 2rem;
  min-height: 60rem;
  min-width: 50rem;
  z-index: 1;
  right: 20rem;
  border-radius: 0.3rem;
  box-shadow: 2px 2px 10px 5px rgba(0, 0, 0, 0.068);
}
.navigation__buyContainer {
  background-color: #1d1d1b;
  display: block;
  padding: 1rem;
  color: white;
  text-align: center;
  font-weight: 700;
  border-radius: 0.3rem;
  margin-bottom: 2rem;
}
.navigation__buyContainer:hover {
  opacity: 70%;
}
.navigation__clearContainer {
  border: solid 1px #1d1d1b;
  display: block;
  padding: 1rem;
  color: #1d1d1b;
  text-align: center;
  font-weight: 700;
  border-radius: 0.3rem;
}
.navigation__clearContainer:hover {
  opacity: 70%;
}
.navigation__arrow {
  transform: rotate(360deg);
  transition: all ease-in-out 0.4s;
}

.submenu {
  position: relative;
}
.submenu__countProduct {
  padding: 0.4rem;
  color: #1d1d1b;
  background-color: white;
  text-align: center;
  border-radius: 0.3rem;
}
.submenu__cartCount {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.submenu #cart {
  display: none;
  cursor: pointer;
}

.submenu:hover #cart {
  display: block;
  position: absolute;
  right: -11.5rem;
  top: 100%;
  z-index: 1;
  background-color: #f6f6f6;
  padding: 20px;
  min-height: 100%;
  min-width: 320px;
  border-radius: 0.3rem;
  box-shadow: 1px 2px 6px 2px rgba(0, 0, 0, 0.068);
}
@media (min-width: 768px) {
  .submenu:hover #cart {
    right: 0;
    min-width: 500px;
  }
}
@media (min-width: 1024px) {
  .submenu:hover #cart {
    right: 0;
    min-height: 100%;
    min-width: 600px;
  }
}

th {
  text-transform: uppercase;
}

td {
  font-size: 2rem;
  text-transform: uppercase;
  border-bottom: 2px solid #e7e7e7;
}
td img {
  max-width: 100%;
}
@media (min-width: 1024px) {
  td img {
    max-width: 50%;
  }
}

.padre {
  text-align: end;
}

.enlace {
  display: inline-block;
  background-color: aqua;
  padding: 2rem;
}

.arrowActive {
  transform: rotate(180deg);
}

.hero {
  margin-top: 10rem;
  background-size: cover;
  height: 40rem;
  width: auto;
  padding: 10rem;
  background: url(../img/bg-store.webp) 50% 27% no-repeat fixed;
}
@media (min-width: 768px) {
  .hero {
    background: url(../img/bg-store.webp) 50% 15% no-repeat fixed;
  }
}
@media (min-width: 1024px) {
  .hero {
    background: url(../img/bg-store.webp) 50% 17% no-repeat fixed;
  }
}

.filter {
  position: absolute;
  left: 1%;
  right: 1%;
  z-index: 1;
  top: 45rem;
  padding: 1rem 1rem;
  max-width: 90%;
  background-color: #1d1d1b;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .filter {
    padding: 2rem 1rem;
    max-width: 80%;
  }
}
@media (min-width: 1024px) {
  .filter {
    max-width: 60%;
    padding: 3rem 1rem;
  }
}
.filter__content {
  display: flex;
  flex-direction: column;
  padding: 0.5rem 2rem;
  justify-content: space-evenly;
  align-items: center;
}
@media (min-width: 768px) {
  .filter__content {
    flex-direction: row;
  }
}
.filter__icon {
  width: auto;
  height: 3rem;
}
.filter__category {
  display: flex;
  gap: 3rem;
  align-items: center;
}
.filter__price {
  display: flex;
  gap: 3rem;
  align-items: center;
}
.filter__text {
  color: white;
  font-size: 1.3rem;
  margin-bottom: 2rem;
  border-bottom: white solid 1px;
}
.filter__link {
  color: white;
  font-size: 2rem;
}
@media (min-width: 1024px) {
  .filter__link {
    font-size: 2.5rem;
  }
}
.filter__link:hover {
  color: lightgray;
}
.filter__active {
  background-color: white;
  color: #1d1d1b;
  padding: 0.5rem;
}

.link--activo {
  background-color: white;
  color: #1d1d1b;
  font-size: 2.5rem;
  padding: 0.5rem 1rem;
}
.link--activo:hover {
  opacity: 60%;
}

.product {
  padding-top: 7rem;
  padding-bottom: 7rem;
  margin-bottom: 0rem;
  margin-top: 4rem;
}
.product__grid {
  display: grid;
  gap: 4rem;
}
@media (min-width: 768px) {
  .product__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .product__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(4, 1fr);
    margin-bottom: 5rem;
    margin-top: 4rem;
  }
}

.card {
  background-color: #f6f6f6;
  border-radius: 0.5rem;
  padding: 1.5rem;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card__banner {
  background-color: #e52257;
  display: flex;
  padding: 0.5rem 2rem;
  border-radius: 0 3px 3px 0;
  position: absolute;
  left: 0;
  top: 1rem;
  width: 90%;
  justify-content: space-between;
}
.card__textBanner {
  margin: 0 1rem;
  color: white;
  font-weight: 200;
}
.card__textBanner:nth-child(2) {
  font-weight: 400;
}
.card__bannerCont {
  display: flex;
}
.card__spaceBanner {
  border-left: solid 2px white;
}
.card__space {
  border: solid 0.1px #999999;
  width: 100%;
}
.card__img {
  margin: 0 auto;
}
.card__des {
  display: flex;
  justify-content: space-between;
}
.card__name {
  margin-bottom: 0.5rem;
  font-weight: 400;
}
.card__price {
  margin-bottom: 0.5rem;
  font-weight: 700;
  color: #e52257;
}
.card__price::before {
  content: "$";
}
.card__category {
  font-size: 1.5rem;
  margin-top: 2px;
}
.card__score {
  display: flex;
  gap: 0.5rem;
}
.card__stars {
  width: auto;
  height: 1.5rem;
  margin-top: 2px;
}
.card__num {
  font-size: 1.5rem;
  margin-top: 2px;
}
.card__btn {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-top: 1rem;
}
.card__addcart {
  background-color: #1d1d1b;
  color: white;
  text-align: center;
  padding: 1.2rem;
  width: 100%;
  border-radius: 0.2rem;
  font-size: 2rem;
}
.card__addcart::after {
  content: " ";
  background-image: url(../img/store.svg);
  background-repeat: no-repeat;
  background-position: bottom center;
  display: inline-block;
  width: 2rem;
  height: 2rem;
  margin-left: 0.5rem;
}
.card__addcart:hover {
  opacity: 90%;
}
.card__active {
  background-color: #e52257;
}
.card__disccount {
  color: #999999;
  font-weight: 200;
  font-size: 1.7rem;
  margin-right: 1rem;
  margin-top: 1rem;
}
.card__comparison {
  display: flex;
  align-items: center;
}

.hiddenProducts {
  display: none;
}

.modal {
  opacity: 0%;
  transition: all ease-in-out 0.3s;
  position: fixed;
  z-index: 10;
  display: flex;
  right: 2%;
  bottom: 10%;
  background-color: #e52257;
  width: 25rem;
  height: auto;
  border-radius: 0.3rem;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  padding: 0.5rem 3rem;
  box-shadow: 2px 2px 7px 2px rgba(0, 0, 0, 0.164);
}
.modal__text {
  color: white;
}
.modal__icon {
  width: 3rem;
}

.modalActive {
  bottom: 20%;
  opacity: 100%;
}

.footer {
  padding-top: 2rem;
  background-color: #1d1d1b;
  position: relative;
}
@media (min-width: 1024px) {
  .footer__content {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__info {
  display: flex;
  flex-direction: column;
  grid-column: 1/3;
}
.footer__headings {
  display: flex;
  justify-content: space-between;
  border-bottom: solid 1px #666666;
}
.footer__heading {
  color: white;
  margin-bottom: 1rem;
}
.footer__links {
  display: flex;
  justify-content: space-between;
  margin-top: 1.5rem;
  border-bottom: solid 1px #686868;
}
.footer__links--content {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-bottom: 1rem;
}
.footer__link {
  color: #cfcfcf;
}
.footer__link:hover {
  color: #acacac;
}
.footer__img {
  grid-column: 3/5;
  position: absolute;
  right: 0;
  top: -10rem;
  visibility: hidden;
}
@media (min-width: 1024px) {
  .footer__img {
    visibility: visible;
  }
}
.footer__img--nike {
  width: 68rem;
}
@media (min-width: 1024px) {
  .footer__img--nike {
    width: 48rem;
  }
}
@media (min-width: 1280px) {
  .footer__img--nike {
    width: 63rem;
  }
}
.footer__social {
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
@media (min-width: 1024px) {
  .footer__social {
    flex-direction: row;
  }
}
.footer__logo {
  width: auto;
  height: 4rem;
}
.footer__icons {
  display: flex;
  gap: 2rem;
  align-items: center;
}
.footer__icon {
  width: auto;
  height: 2rem;
}
.footer__icon:hover {
  opacity: 70%;
}
.footer__copy {
  color: white;
  font-weight: 700;
}
.footer__copy span {
  display: none;
}
@media (min-width: 1280px) {
  .footer__copy span {
    font-weight: 200;
    display: inline;
  }
}
.footer__copy::after {
  content: "";
  border-right: solid 2px white;
  margin-left: 5px;
}
@media (min-width: 1024px) {
  .footer__copy::after {
    margin-left: 30px;
    margin-right: 10px;
  }
}

.behance {
  filter: brightness(60%);
}

.btn__chat {
  position: fixed;
  bottom: 5rem;
  right: -14px;
  transition-property: transform;
  transition-duration: 0.3s;
  transition-delay: 0.1s;
}
.btn__chat:hover {
  transform: scale(1.05);
}
@media (min-width: 1024px) {
  .btn__chat {
    bottom: 1rem;
    right: 1rem;
  }
}

.mobile {
  display: block;
  transition: all 0.25s ease-in;
  position: fixed;
  top: -8rem;
  background-color: #363636;
  padding: 3rem;
  width: 100%;
}
@media (min-width: 1024px) {
  .mobile {
    display: none;
  }
}
.mobile__grid {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
}
.mobile__link {
  color: white;
  padding: 1rem;
  text-align: center;
}
.mobile__link:hover {
  background-color: #777777;
  width: 100%;
}
.mobile__separador {
  border: solid #8d8d8d 1px;
  width: 100%;
}

.active {
  z-index: 2;
  top: 23rem;
}
@media (min-width: 768px) {
  .active {
    top: 20rem;
  }
}

.home {
  background: url(../img/headerBg.webp);
  background-size: cover;
  height: 45rem;
  width: auto;
  margin-top: 20rem;
  background-position: left;
  position: relative;
  padding: 5rem;
}
@media (min-width: 768px) {
  .home {
    height: 30rem;
  }
}
.home__info {
  text-align: left;
}
.home__text {
  text-align: left;
  margin-top: 1rem;
}
.home__heading {
