@use "base/variables" as v;
@use "base/mixins" as m;

.home {
  background: url(../img/headerBg.webp);
  background-size: cover;
  height: 45rem;

  @include m.tablet {
    height: 30rem;
  }
  width: auto;
  margin-top: 20rem;
  background-position: left;
  position: relative;
  padding: 5rem;
  &__info {
    text-align: left;
  }
  &__text {
    text-align: left;
    margin-top: 1rem;
  }
  &__heading {
    margin-bottom: 0;
    font-weight: 300;
    font-size: 7rem;
    span {
      font-weight: bold;
    }
  }
  &__subheading {
    font-size: 3rem;
    font-weight: 400;
    border-bottom: solid rgb(175, 175, 175) 1px;
  }
  &__para {
    text-align: left;
    margin-top: 0;
  }
  &__btn {
    background-color: v.$primario;
    color: white;
    padding: 1.2rem;
    width: auto;
    border-radius: 0.2rem;
    font-size: 2rem;
    display: inline-block;

    &:hover {
      opacity: 90%;
    }
  }
  &__img {
    display: none;
    @include m.desktop {
      display: block;
    }
    position: absolute;
    right: 0;
    width: 60rem;
    bottom: -7rem;
  }
  &__product {
    margin-top: 0;
    padding: 0;
  }
}
