@use "base/variables" as v;
@use "base/mixins" as m;

.hero {
  margin-top: 10rem;
  background-size: cover;
  height: 40rem;
  width: auto;
  padding: 10rem;
  background: url(../img/bg-store.webp) 50% 27% no-repeat fixed;
  @include m.tablet {
    background: url(../img/bg-store.webp) 50% 15% no-repeat fixed;
  }
  @include m.desktop {
    background: url(../img/bg-store.webp) 50% 17% no-repeat fixed;
  }
}

.filter {
  position: absolute;
  left: 1%;
  right: 1%;
  z-index: 1;
  top: 45rem;
  padding: 1rem 1rem;
  max-width: 90%;
  background-color: v.$primario;
  margin: 0 auto;
  @include m.tablet {
    padding: 2rem 1rem;
    max-width: 80%;
  }
  @include m.desktop {
    max-width: 60%;
    padding: 3rem 1rem;
  }

  &__content {
    display: flex;
    flex-direction: column;
    padding: 0.5rem 2rem;
    justify-content: space-evenly;
    align-items: center;
    @include m.tablet {
      flex-direction: row;
    }
  }
  &__icon {
    width: auto;
    height: 3rem;
  }
  &__category {
    display: flex;
    gap: 3rem;
    align-items: center;
  }
  &__price {
    display: flex;
    gap: 3rem;
    align-items: center;
  }
  &__text {
    color: white;
    font-size: 1.3rem;
    margin-bottom: 2rem;
    border-bottom: white solid 1px;
  }
  &__link {
    color: white;
    font-size: 2rem;
    @include m.desktop {
      font-size: 2.5rem;
    }
    &:hover {
      color: rgb(211, 211, 211);
    }
  }
  &__active {
    background-color: white;
    color: v.$primario;
    padding: 0.5rem;
  }
}

.link--activo {
  background-color: white;
  color: v.$primario;
  font-size: 2.5rem;
  padding: 0.5rem 1rem;
  &:hover {
    opacity: 60%;
  }
}
