@use "base/variables" as v;
@use "base/mixins" as m;

.product {
  padding-top: 7rem;
  padding-bottom: 7rem;
  margin-bottom: 0rem;
  margin-top: 4rem;

  &__grid {
    display: grid;
    gap: 4rem;
    @include m.tablet {
      @include m.grid(2, 4rem);
    }
    @include m.desktop {
      @include m.grid(4, 4rem);
      margin-bottom: 5rem;
      margin-top: 4rem;
    }
  }
}
.card {
  background-color: v.$grisClaro;
  border-radius: 0.5rem;
  padding: 1.5rem;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__banner {
    background-color: v.$secundario;
    display: flex;
    padding: 0.5rem 2rem;
    border-radius: 0 3px 3px 0;
    position: absolute;
    left: 0;
    top: 1rem;
    width: 90%;
    justify-content: space-between;
  }
  &__textBanner {
    margin: 0 1rem;
    color: white;
    font-weight: 200;
    &:nth-child(2) {
      font-weight: 400;
    }
  }
  &__bannerCont {
    display: flex;
  }
  &__spaceBanner {
    border-left: solid 2px white;
  }

  &__space {
    border: solid 0.1px #999999;
    width: 100%;
  }
  &__img {
    margin: 0 auto;
  }
  &__des {
    display: flex;
    justify-content: space-between;
  }
  &__name {
    margin-bottom: 0.5rem;
    font-weight: 400;
  }
  &__price {
    margin-bottom: 0.5rem;
    font-weight: 700;
    color: v.$secundario;
    &::before {
      content: "$";
    }
  }
  &__category {
    font-size: 1.5rem;
    margin-top: 2px;
  }
  &__score {
    display: flex;
    gap: 0.5rem;
  }
  &__stars {
    width: auto;
    height: 1.5rem;
    margin-top: 2px;
  }
  &__num {
    font-size: 1.5rem;
    margin-top: 2px;
  }

  &__btn {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-top: 1rem;
  }
  &__addcart {
    background-color: v.$primario;
    color: white;
    text-align: center;
    padding: 1.2rem;
    width: 100%;
    border-radius: 0.2rem;
    font-size: 2rem;
    &::after {
      content: " ";
      background-image: url(../img/store.svg);
      background-repeat: no-repeat;
      background-position: bottom center;
      display: inline-block;
      width: 2rem;
      height: 2rem;
      margin-left: 0.5rem;
    }
    &:hover {
      opacity: 90%;
    }
  }

  &__active {
    background-color: v.$secundario;
  }

  &__disccount {
    color: #999999;
    font-weight: 200;
    font-size: 1.7rem;
    margin-right: 1rem;
    margin-top: 1rem;
  }
  &__comparison {
    display: flex;
    align-items: center;
  }
}

.hiddenProducts {
  display: none;
}
.modal {
  opacity: 0%;
  transition: all ease-in-out 0.3s;
  position: fixed;
  z-index: 10;
  display: flex;
  right: 2%;
  bottom: 10%;
  background-color: v.$secundario;
  width: 25rem;
  height: auto;
  border-radius: 0.3rem;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  padding: 0.5rem 3rem;
  box-shadow: 2px 2px 7px 2px rgba(0, 0, 0, 0.164);
  &__text {
    color: white;
  }
  &__icon {
    width: 3rem;
  }
}

.modalActive {
  bottom: 20%;
  opacity: 100%;
}
