@use "base/variables" as v;
@use "base/mixins" as m;

.login {
  width: 100%;
  height: auto;
  &__product {
    margin-top: 2rem;
    padding-top: 1rem;
  }

  &__forms {
    justify-content: center;
    align-items: center;
    display: flex;
  }
  &__container {
    display: flex;
    flex-direction: column;
    margin-top: 30rem;
    width: 70rem;
    height: auto;
    align-items: center;
    justify-content: center;
    background-color: rgb(246, 246, 246);
    box-shadow: 2px 5px 7px 2px rgba(0, 0, 0, 0.123);
    padding: 3rem;
    border-radius: 0.3rem;
  }
  &__form {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
  }
  &__heading {
    text-align: center;
    font-weight: 300;
    margin-bottom: 2rem;
  }
  &__label {
    text-transform: uppercase;
  }
  &__input {
    background: none;
    width: 25rem;
    margin-bottom: 1.5rem;
    @include m.tablet {
      width: 55rem;
    }
    height: 3.5rem;
    border: none;
    border-bottom: 1px solid rgb(189, 189, 189);
  }
  &__submit {
    display: block;
    border: none;
    background-color: v.$primario;
    color: white;
    padding: 1rem 0;
    cursor: pointer;
    margin-top: 1rem;
    font-size: 1.8rem;
    border-radius: 0.3rem;
    &:hover {
      opacity: 70%;
    }
  }
  &__links {
    display: flex;
    gap: 2rem;
    @include m.tablet {
      gap: 25rem;
    }
    align-items: center;
  }
  &__link {
    margin-top: 2rem;
    color: v.$primario;
    font-size: 1.8rem;
    &:hover {
      opacity: 70%;
      border-bottom: solid 1px v.$primario;
    }
  }
}

.newA {
  background-color: v.$primario;
  color: white;
  &__s {
    background-color: white;
    color: v.$primario;
  }
  &__in {
    color: rgb(206, 206, 206);
  }
  &__a {
    color: white;
    &:hover {
      border-bottom: solid 1px white;
    }
  }
}

.alert {
  text-align: center;
  color: rgb(182, 71, 71);
  margin: 3rem 0;
  font-size: 1.8rem;
}
.bg-red {
  background-color: aqua;
}
