@use "base/variables" as v;
@use "base/mixins" as m;

.faq {
  background-color: v.$grisOscuro;
  padding: 5rem 0rem;

  &__heading {
    color: white;
    font-size: 3rem;
    font-weight: 400;
    border-bottom: 1px solid rgba(228, 228, 228, 0.247);
    margin-bottom: 2rem;
  }

  &__acordeon {
    display: flex;
    justify-content: space-between;
    color: white;
    gap: 2rem;
    align-items: center;
  }
  &__text {
    color: white;
    font-weight: 400;
    text-align: left;
    font-size: 2rem;
  }
  &__btn {
    color: white;
    cursor: pointer;
    font-weight: 200;
    font-size: 3rem;
    transition: 0.7s;
  }
  &__content {
    border-bottom: 1px solid rgba(228, 228, 228, 0.247);
    height: 7.5rem;
    transition: 0.7s;
    margin-bottom: 1rem;
    overflow: hidden;
    @include m.desktop {
      &:last-of-type {
        margin-bottom: 7rem;
      }
    }
  }
  &__par {
    margin: 0;
    color: rgba(255, 255, 255, 0.836);
    font-weight: 200;
    text-align: left;
    font-size: 1.8rem;
    line-height: 1.5;
    span {
      font-weight: 400;
      display: block;
    }
  }
}

.openAcordeon {
  height: 32rem;
  @include m.desktop {
    height: 18rem;
  }
}
.rotateBtn {
  transform: rotate(135deg);
}
