@use "base/variables" as v;
@use "base/mixins" as m;

.footer {
  padding-top: 2rem;
  background-color: v.$primario;
  position: relative;

  &__content {
    @include m.desktop {
      @include m.grid(4, 2rem);
    }
  }
  &__info {
    display: flex;
    flex-direction: column;
    grid-column: 1 / 3;
  }
  &__headings {
    display: flex;
    justify-content: space-between;
    border-bottom: solid 1px rgb(102, 102, 102);
  }
  &__heading {
    color: white;
    margin-bottom: 1rem;
  }
  &__links {
    display: flex;
    justify-content: space-between;
    margin-top: 1.5rem;
    border-bottom: solid 1px rgb(104, 104, 104);
  }
  &__links--content {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    margin-bottom: 1rem;
  }
  &__link {
    color: rgb(207, 207, 207);
    &:hover {
      color: rgb(172, 172, 172);
    }
  }
  &__img {
    grid-column: 3 / 5;
    position: absolute;
    right: 0;
    top: -10rem;
    visibility: hidden;
    @include m.desktop {
      visibility: visible;
    }
  }
  &__img--nike {
    width: 68rem;
    @include m.desktop {
      width: 48rem;
    }
    @include m.desktopXL {
      width: 63rem;
    }
  }

  &__social {
    display: flex;
    flex-direction: column-reverse;
    @include m.desktop {
      flex-direction: row;
    }
    justify-content: space-between;
    align-items: center;
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
  &__logo {
    width: auto;
    height: 4rem;
  }
  &__icons {
    display: flex;
    gap: 2rem;
    align-items: center;
  }
  &__icon {
    width: auto;
    height: 2rem;
    &:hover {
      opacity: 70%;
    }
  }
  &__copy {
    color: white;
    font-weight: 700;
    span {
      display: none;
    }

    @include m.desktopXL {
      span {
        font-weight: 200;
        display: inline;
      }
    }

    &::after {
      content: "";
      border-right: solid 2px white;
      margin-left: 5px;
      @include m.desktop {
        margin-left: 30px;
        margin-right: 10px;
      }
    }
  }
}
.behance {
  filter: brightness(60%);
}
